import React, { useState } from 'react';
import { cartContext } from '../../Context/CartContext';
import toast from 'react-hot-toast';
import { Link } from 'react-router-dom';

function CartItem({ cart }) {
  const { updateQuantity, deleteFromCart } = React.useContext(cartContext);

  const [isUpdating, setIsUpdating] = useState('false');

  const [isDeletingCart, setIsDeletingCart] = useState(false);

  async function handleUpdateQuantity(productId, quantity) {
    const res = await updateQuantity(productId, quantity);
    if (res) {
      setIsUpdating('false');
    }
  }

  async function handleDeleteFromCart(productId) {
    const res = await deleteFromCart(productId);

    if (res) {
      setIsDeletingCart(false);
      toast.success('Deleted Successfully', {
        position: 'bottom-center',
        duration: 2000,
        style: {
          border: '1px solid #713200',
          padding: '16px',
          color: '#713200',
        },
      });
    } else {
      setIsDeletingCart(false);
    }
  }

  let productSlug = cart?.product?.title
    .replace(/\//g, '')
    .split(' ')
    .join('-');

  return (
    <>
      <img
        src={cart?.product?.imageCover}
        alt=""
        className="w-20 rounded object-cover"
      />

      <div>
        <div className="flex flex-col gap-y-5 text-[10px] ">
          <div>
            <Link to={`/product/${productSlug}/${cart?.product?._id}`}>
              <h3 className="text-2xl mb-3 font-bold line-clamp-1">
                {cart?.product?.title}
              </h3>
            </Link>
            <span className="text-xs font-semibold badge badge-primary">
              {cart.product?.brand?.name}
            </span>
          </div>

          <div>
            <span className="text-lg font-bold">Price: </span>
            <span className="text-lg text-primary">
              {(cart?.price * cart?.count).toFixed(2)} EGP
            </span>
          </div>
        </div>
      </div>

      <div className="flex flex-1 items-center justify-end gap-5">
        <div className="flex items-center rounded-xl border">
          <button
            disabled={isUpdating === 'true'}
            onClick={() => {
              setIsUpdating('true');
              handleUpdateQuantity(cart?.product?._id, cart?.count + 1);
            }}
            className={`btn btn-ghost disabled:bg-transparent`}
          >
            <svg
              className="w-6 h-6"
              aria-hidden="true"
              xmlns="http://www.w3.org/2000/svg"
              width={24}
              height={24}
              fill="none"
              viewBox="0 0 24 24"
            >
              <path
                stroke="currentColor"
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth={2}
                d="M5 12h14m-7 7V5"
              />
            </svg>
          </button>

          <div className="w-10 text-center bg-transparent flex items-center justify-center">
            {isUpdating === 'true' ? (
              <span className="loading loading-spinner loading-md text-primary"></span>
            ) : (
              cart?.count
            )}
          </div>
          <button
            onClick={() => {
              setIsUpdating('true');
              handleUpdateQuantity(cart?.product?._id, cart?.count - 1);
            }}
            disabled={cart?.count === 1 || isUpdating === 'true'}
            className="btn btn-ghost disabled:bg-transparent"
          >
            <svg
              className="w-6 h-6"
              aria-hidden="true"
              xmlns="http://www.w3.org/2000/svg"
              width="24"
              height="24"
              fill="none"
              viewBox="0 0 24 24"
            >
              <path
                stroke="currentColor"
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth="2"
                d="M5 12h14"
              />
            </svg>
          </button>
        </div>
        <button
          onClick={() => {
            setIsDeletingCart(true);
            handleDeleteFromCart(cart?.product?._id);
          }}
          className="btn-circle btn transition hover:bg-red-600 hover:text-white"
          disabled={isDeletingCart}
        >
          <span className="sr-only">Remove item</span>

          {isDeletingCart ? (
            <span className="loading loading-spinner loading-md"></span>
          ) : (
            <svg
              xmlns="http://www.w3.org/2000/svg"
              fill="none"
              viewBox="0 0 24 24"
              strokeWidth="1.5"
              stroke="currentColor"
              className="h-4 w-4"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                d="M14.74 9l-.346 9m-4.788 0L9.26 9m9.968-3.21c.342.052.682.107 1.022.166m-1.022-.165L18.16 19.673a2.25 2.25 0 01-2.244 2.077H8.084a2.25 2.25 0 01-2.244-2.077L4.772 5.79m14.456 0a48.108 48.108 0 00-3.478-.397m-12 .562c.34-.059.68-.114 1.022-.165m0 0a48.11 48.11 0 013.478-.397m7.5 0v-.916c0-1.18-.91-2.164-2.09-2.201a51.964 51.964 0 00-3.32 0c-1.18.037-2.09 1.022-2.09 2.201v.916m7.5 0a48.667 48.667 0 00-7.5 0"
              />
            </svg>
          )}
        </button>
      </div>
    </>
  );
}

export default CartItem;
