import axios from 'axios';
import { useFormik } from 'formik';
import React, { useEffect, useState } from 'react';
import toast from 'react-hot-toast';
import { Outlet, useNavigate } from 'react-router-dom';
import * as Yup from 'yup';
function Forget({ children }) {
  const navigate = useNavigate();


  const [confirmMsg, setConfirmMsg] = useState(false);

  let [countDown, setCountDown] = useState(0);

  async function forgetPassword(body) {
    try {
      const response = await axios.post(
        'https://ecommerce.routemisr.com/api/v1/auth/forgotPasswords',
        body
      );
      const data = await response.data;
      navigate('verify-reset-code');
      setConfirmMsg(true);
      setCountDown(60);
      const interval = setInterval(() => {
        setCountDown((prevCountDown) => {
          if (prevCountDown === 0) {
            clearInterval(interval);
            setConfirmMsg(false);
            return prevCountDown;
          }
          return prevCountDown - 1;
        });
      }, 1000);
      toast.success(data.message, {
        duration: 3000,
        position: 'bottom-center',
        style: {
          border: '1px solid green',
          padding: '16px',
          color: 'black',
        },
      });
    } catch (error) {
      toast.error(error.response.data.message, {
        duration: 3000,
        position: 'bottom-center',
        style: {
          border: '1px solid red',
          padding: '16px',
          color: 'black',
        },
      });
    }
  }

  const validationSchema = Yup.object().shape({
    email: Yup.string().required('Email is required').email('Email is invalid'),
  });

  const initialValues = {
    email: '',
  };

  const onSubmit = (values) => {
    forgetPassword(values);
  };

  const formik = useFormik({
    initialValues: initialValues,
    validationSchema: validationSchema,
    onSubmit: onSubmit,
  });
  
useEffect(() => {
  document.title = 'Forget Password - Ecommerce'
})

  return (
    <div className="container mx-auto py-20 px-5  h-screen">
      <h1 className="text-3xl md:text-6xl font-bold my-20 text-center">
        🔐Forget Password
      </h1>
      <form onSubmit={formik.handleSubmit} className="flex flex-col gap-3">
        <input
          type="email"
          placeholder="Email"
          className="input input-bordered"
          onChange={formik.handleChange}
          onBlur={formik.handleBlur}
          value={formik.values.email}
          name="email"
        />
        {formik.errors.email && formik.touched.email && (
          <div role="alert" className="alert alert-error">
            <svg
              xmlns="http://www.w3.org/2000/svg"
              className="stroke-current shrink-0 h-6 w-6"
              fill="none"
              viewBox="0 0 24 24"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth="2"
                d="M10 14l2-2m0 0l2-2m-2 2l-2-2m2 2l2 2m7-2a9 9 0 11-18 0 9 9 0 0118 0z"
              />
            </svg>
            <span>{formik.touched.email && formik.errors.email}</span>
          </div>
        )}
        <button
          disabled={!formik.isValid || confirmMsg}
          type="submit"
          className="btn btn-primary"
        >
          {confirmMsg ? (
            <span className="countdown font-mono text-xl">
              <span style={{ '--value': countDown }}></span>
            </span>
          ) : (
            'Send'
          )}
        </button>
        {/* <Link to="verify-reset-code">
        </Link> */}
      </form>

      <Outlet />
      {children}
    </div>
  );
}

export default Forget;
