import React from 'react';
import Slider from 'react-slick';
import { CategoriesClient } from '../../APIs/CategoriesApi';
import { useQuery } from '@tanstack/react-query';
import SkeletonEffect from '../Loaders/SkeletonEffect';

function CategorySlider() {
  const settings = {
    dots: false,
    infinite: true,
    speed: 500,
    slidesToShow: 6,
    slidesToScroll: 1,
    arrows: false,
    autoplay: true,
    autoplaySpeed: 3000,
    responsive: [
      {
        breakpoint: 3000,
        settings: {
          slidesToShow: 6,
          slidesToScroll: 1,
        },
      },
      {
        breakpoint: 1280,
        settings: {
          slidesToShow: 4,
          slidesToScroll: 4,
        },
      },
      {
        breakpoint: 992,
        settings: {
          slidesToShow: 3,
          slidesToScroll: 3,
        },
      },
      {
        breakpoint: 540,
        settings: {
          slidesToShow: 2,
          slidesToScroll: 2,
        },
      },
      {
        breakpoint: 400,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
        },
      },
    ],
  };

  function getCategories() {
    return CategoriesClient.get();
  }

  const { data, isLoading } = useQuery({
    queryKey: ['allCategories'],
    queryFn: () => getCategories(),
  });

  let categories = data?.data?.data;

  if (isLoading) {
    return <SkeletonEffect />;
  }

  return (
    <div className="slider-container my-10">
      <Slider {...settings}>
        {categories?.map((category) => (
          <div
            key={category._id}
            className="hover:scale-105 py-2 transition duration-300 overflow-hidden"
          >
            <figure>
              <img
                src={category.image}
                alt=""
                className="w-full h-full object-center mx-auto object-cover rounded-full shadow-lg"
                style={{ height: '200px', width: '200px' }}
              />
            </figure>
            <p className="text-center text-lg font-semibold mt-2">
              {category.name}
            </p>
          </div>
        ))}
      </Slider>
    </div>
  );
}

export default CategorySlider;
