import React from 'react';

function NavigateToUp() {
  const [scroll, setScroll] = React.useState(0);

  const scrollToTop = () => {
    window.scrollTo({
      top: 0,
      behavior: 'smooth',
    });
  };
  // Listen for scroll events
  window.addEventListener('scroll', () => {
    setScroll(window.scrollY);
  });

  // Clean up the time interval

  return (
    <div className="fixed bottom-5 right-5 animate-bounce z-50">
      {scroll > 500 && (
        <button
          onClick={scrollToTop}
          className="btn btn-primary btn-circle border-white hover:border-white border-4 shadow-lg"
        >
          <svg
            className="w-6 h-6 dark:text-white"
            aria-hidden="true"
            xmlns="http://www.w3.org/2000/svg"
            width={24}
            height={24}
            fill="none"
            viewBox="0 0 24 24"
          >
            <path
              stroke="currentColor"
              strokeLinecap="round"
              strokeLinejoin="round"
              strokeWidth={2}
              d="M12 6v13m0-13 4 4m-4-4-4 4"
            />
          </svg>
        </button>
      )}
    </div>
  );
}

export default NavigateToUp;
