import React from 'react';
import { RouterProvider, createHashRouter } from 'react-router-dom';
import Layout from './Layout/Layout';
import Register from './Components/Register/Register';
import Login from './Components/Login/Login';
import Products from './Components/Products/Products';
import NotFound from './Components/NotFound';
import Cart from './Components/Cart/Cart';
import Wishlist from './Components/Wishlist/Wishlist';
import AuthContextProvider from './Context/AuthContext';
import ProtectedRoute from './Components/ProtectedRoute';
import 'slick-carousel/slick/slick.css';
import 'slick-carousel/slick/slick-theme.css';
import Home from './Components/Home/Home';
import { QueryClient, QueryClientProvider } from '@tanstack/react-query';
import ProductDetails from './Components/Products/ProductDetails';
import CartContextProvider from './Context/CartContext';
import WishContextProvider from './Context/WishContext';
import Checkout from './Components/Checkout/Checkout';
import Orders from './Components/Orders/Orders';
import ProfilePage from './Components/ProfilePage/ProfilePage';
import Forget from './Components/FogetPassword/Forget';
import GetResetCode from './Components/FogetPassword/GetResetCode';
import ResetPassword from './Components/FogetPassword/ResetPassword';


const router = createHashRouter([
  {
    path: '/',
    element: <Layout />,
    children: [
      { index: true, element: <Home /> },
      { path: 'register', element: <Register /> },
      { path: 'login', element: <Login /> },
      { path: 'product/:name/:id', element: <ProductDetails /> },
      { path: 'products', element: <Products /> },
      {
        path: 'cart',
        element: (
          <ProtectedRoute>
            <Cart />
          </ProtectedRoute>
        ),
      },
      {
        path: 'profile',
        element: (
          <ProtectedRoute>
            <ProfilePage />
          </ProtectedRoute>
        ),
      },
      {
        path: 'wishlist',
        element: (
          <ProtectedRoute>
            <Wishlist />
          </ProtectedRoute>
        ),
      },
      {
        path: 'checkout/:cartid',
        element: (
          <ProtectedRoute>
            <Checkout />
          </ProtectedRoute>
        ),
      },
      {
        path: 'allorders',
        element: (
          <ProtectedRoute>
            <Orders />
          </ProtectedRoute>
        ),
      },
      {
        path: 'forget-password',
        element: <Forget />,
        children: [
          {
            path: 'verify-reset-code',
            element: <GetResetCode />,
            children: [
              {
                path: 'reset-password',
                element: <ResetPassword />,
              },
            ],
          },
        ],
      },
      { path: '*', element: <NotFound /> },
    ],
  },
]);

const queryClient = new QueryClient();

function App() {
  return (
    <AuthContextProvider>
      <QueryClientProvider client={queryClient}>
        <CartContextProvider>
          <WishContextProvider>
            <RouterProvider router={router} />
          </WishContextProvider>
        </CartContextProvider>
      </QueryClientProvider>
    </AuthContextProvider>
  );
}

export default App;
