import React from 'react';
import Slider from 'react-slick';
import img from '../Assets/slider1.jpg';
import img2 from '../Assets/slider2.jpg';
function SimpleSlider() {
  const settings = {
    dots: false,
    infinite: true,
    arrows: false,
    autoplay: true,
    speed: 500,
    autoplaySpeed: 3000,
    responsive: [
      {
        breakpoint: 3000,
        settings: {
          slidesToShow: 2,
          slidesToScroll: 1,
        },
      },
      {
        breakpoint: 1280,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
        },
      },
    ],
  };
  return (
    <div className="slider-container">
      <Slider {...settings}>
        <div className="h-[250px] lg:h-[450px] md:h-[350px] sm:h-[450px]">
          <img src={img} alt="" className="w-full h-full" />
        </div>
        <div className="h-[250px] lg:h-[450px] md:h-[350px] sm:h-[450px]">
          <img src={img2} alt="" className="w-full h-full" />
        </div>
      </Slider>
    </div>
  );
}

export default SimpleSlider;
