import React from 'react';

function Pagination({ pages, setPageNumber, setProductsByBrands }) {
  const handlePaging = (e) => {
    setPageNumber(Number(e.target.innerText));
    window.scrollTo({ top: 0, behavior: 'smooth' });
    setProductsByBrands([]);
  };

  return (
    <div className="join">
      {Array.from({ length: pages }, (_, index) => (
        <button onClick={handlePaging} key={index} className="join-item btn">
          {index + 1}
        </button>
      ))}
    </div>
  );
}

export default Pagination;
