import React, { useEffect, useState } from 'react';
import { ProductsClient } from '../../APIs/ProductsApi';
import SingleProduct from './SingleProduct';
import SimpleSlider from '../Slider';
import Brands from '../Brands/Brands';
import { useQuery } from '@tanstack/react-query';
import PageLoader from '../PageLoader';
import Pagination from '../Pagination/Pagination';
import ErrorPage from './../ErrorPage';

function Products() {
  
  const [productsByBrands, setProductsByBrands] = useState([]);
  const [pageNumber, setPageNumber] = useState(1);

  const productsByPage = async (pageNum = pageNumber) => {
    return ProductsClient.get(`?limit=40&page=${pageNumber}`);
  };

  const { data, error, isError, isLoading } = useQuery({
    queryKey: ['allProducts', pageNumber],
    queryFn: () => productsByPage(pageNumber),
    keepPreviousData: true,
  });

  let allProducts = data?.data?.data;
  const pages = data?.data?.metadata.numberOfPages;

  useEffect(() => {
    document.title = 'Products - Ecommerce';
  })

  if (isError) {
    return <ErrorPage error={error} />;
  }

  return (
    <>
      <div className="mx-auto">
        <SimpleSlider />
      </div>
      {isLoading ? (
        <PageLoader />
      ) : (
        <>
          <div className="container mx-auto">
            <div className="my-10 mx-3">
              <Brands
                pageNumber={pageNumber}
                allProducts={allProducts}
                setProductsByBrands={setProductsByBrands}
              />
              <div className="grid sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 xl:grid-cols-5 gap-3">
                {productsByBrands.length
                  ? productsByBrands.map((product) => (
                      <SingleProduct key={product.id} product={product} />
                    ))
                  : allProducts.map((product) => (
                      <SingleProduct key={product.id} product={product} />
                    ))}
              </div>
            </div>
            <div className="my-10 flex justify-center">
              <Pagination
                pages={pages}
                setPageNumber={setPageNumber}
                setProductsByBrands={setProductsByBrands}
              />
            </div>
          </div>
        </>
      )}
    </>
  );
}

export default Products;
