import React from 'react';

function CartSkeleton() {
  return (
    <li className="flex flex-col sm:flex-row gap-3 items-center text-center sm:text-left border rounded-xl p-10">
      <div className="skeleton size-32 rounded"></div>
      <div className="flex flex-col md:items-start md:justify-start items-center justify-center gap-y-5">
        <div className="skeleton w-[120px] h-[20px] md:w-[500px] md:h-[20px] "></div>
        <div className="skeleton w-[50px] h-[20px] md:w-[100px] md:h-[20px] "></div>
      </div>
      <div>
        <div className="flex flex-col gap-y-5 text-[10px] ">
          <div>
            <div className="h-5 bg-gray-200 rounded w-3/4 mb-3"></div>
            <span className="h-3 bg-gray-200 rounded w-1/4"></span>
          </div>
          <div>
            <span className="h-5 bg-gray-200 rounded w-1/2"></span>
          </div>
        </div>
      </div>
      <div className="flex flex-1 items-center justify-end gap-5">
        <div className="flex items-center rounded-xl border">
          <button className="btn btn-ghost disabled:bg-transparent h-6 w-6"></button>
          <div className="w-10 text-center bg-transparent flex items-center justify-center h-6"></div>
          <button className="btn btn-ghost disabled:bg-transparent h-6 w-6"></button>
        </div>
        <button className="skeleton size-10 rounded-full transition"></button>
      </div>
    </li>
  );
}

export default CartSkeleton;
