import axios from 'axios';
import { useFormik } from 'formik';
import React, { useState } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import * as Yup from 'yup';
import styles from './Register.module.css';

function Register() {
  const [loading, setLoading] = useState(false);

  const passRegex =
    /^(?=.*?[A-Z])(?=.*?[a-z])(?=.*?[0-9])(?=.*?[#?!@$ %^&*-]).{6,12}$/;
  const phoneRegex = /^(010|011|012|015)[0-9]{8}$/;

  const validationSchema = Yup.object({
    name: Yup.string()
      .required('Name is required')
      .min(3, 'Name must atleast has 3 characters')
      .max(15, 'Name must be atmost 15 characters'),
    email: Yup.string()
      .email('Invalid email address')
      .required('Email is required'),
    password: Yup.string()
      .required(`Password can't be empty`)
      .min(6, 'Password must be atleast 6 characters')
      .max(12, 'Password must be atmost 12 characters')
      .matches(
        passRegex,
        'Minimum 6 characters and Maximum 12 characters, at least one upper case English letter, one lower case English letter, one number and one special character'
      ),
    rePassword: Yup.string()
      .oneOf([Yup.ref('password')], 'Passwords are not matching')
      .required('Please re-enter your password'),
    phone: Yup.string()
      .matches(
        phoneRegex,
        'Egyptian mobile phone must start with  (010, 011, 012, or 015) followed by 8 digits'
      )
      .required('Phone is required'),
  });

  const [errorMsg, setErrorMsg] = useState(false);
  const [confirmMsg, setConfirmMsg] = useState(false);
  const navigate = useNavigate()
  const sendUserData = (body) => {
    setLoading(true);
    axios
      .post('https://ecommerce.routemisr.com/api/v1/auth/signup', body)
      .then(({ data }) => {
        setLoading(false);
        setConfirmMsg('Account created successfuly!');
        setTimeout(() => {
          setConfirmMsg(false);
          navigate('/login');
        }, 3000);
      })
      .catch(({ response }) => {
        setLoading(false);
        setErrorMsg(response.data.message);
        setTimeout(() => {
          setErrorMsg(false);
        }, 3000);
      });
  };

  function onSubmit(values) {
    //data sent to backend
    sendUserData(values);
  }

  const initialValues = {
    name: '',
    email: '',
    password: '',
    rePassword: '',
    phone: '',
  };

  const { handleSubmit, handleChange, handleBlur, touched, errors, values } =
    useFormik({
      initialValues: initialValues,
      onSubmit,
      validationSchema,
    });

  return (
    <>
      <div className={`${styles.bgImage} hero min-h-screen bg-base-50`}>
        <div className="hero-content flex-col">
          <div className="text-center">
          </div>
          <div className="card shrink-0 w-[91vw] md:w-[410px] lg:w-[620px] shadow-2xl bg-base-100">
            <form onSubmit={handleSubmit} className="card-body">
              <div className="form-control">
                <label htmlFor="name" className="form-control mt-3">
                  <div className="label">
                    <span className="label-text">Name</span>
                  </div>
                  <input
                    onChange={handleChange}
                    onBlur={handleBlur}
                    value={values.name}
                    type="text"
                    id="name"
                    placeholder="john doe"
                    className={
                      errors.name && touched.name
                        ? `input input-bordered input-error`
                        : `input input-bordered`
                    }
                  />
                  {errors.name && touched.name && (
                    <div className="label">
                      <span className="label-text-alt text-red-500">
                        {errors.name + ''}
                      </span>
                    </div>
                  )}
                </label>
                <label htmlFor="email" className="form-control mt-3">
                  <div className="label">
                    <span className="label-text">Email</span>
                  </div>
                  <input
                    onChange={handleChange}
                    onBlur={handleBlur}
                    value={values.email}
                    autoComplete="username"
                    type="email"
                    id="email"
                    placeholder="example@yahoo.com"
                    className={
                      errors.email && touched.email
                        ? `input input-bordered input-error`
                        : `input input-bordered`
                    }
                  />
                  {errors.email && touched.email && (
                    <div className="label">
                      <span className="label-text-alt text-red-500">
                        {errors.email + ''}
                      </span>
                    </div>
                  )}
                </label>
                <label htmlFor="password" className="form-control mt-3">
                  <div className="label">
                    <span className="label-text">Password</span>
                  </div>
                  <input
                    onChange={handleChange}
                    onBlur={handleBlur}
                    value={values.password}
                    autoComplete="on"
                    type="password"
                    id="password"
                    placeholder="password"
                    className={
                      errors.password && touched.password
                        ? `input input-bordered input-error`
                        : `input input-bordered`
                    }
                  />
                  {errors.password && touched.password && (
                    <div className="label">
                      <span className="label-text-alt text-red-500">
                        {errors.password + ''}
                      </span>
                    </div>
                  )}
                </label>

                <label htmlFor="rePassword" className="form-control mt-3">
                  <div className="label">
                    <span className="label-text">Re-Password</span>
                  </div>
                  <input
                    onChange={handleChange}
                    onBlur={handleBlur}
                    value={values.rePassword}
                    autoComplete="current-password"
                    type="password"
                    id="rePassword"
                    placeholder="re-password"
                    className={
                      errors.rePassword && touched.rePassword
                        ? `input input-bordered input-error`
                        : `input input-bordered`
                    }
                  />
                  {errors.rePassword && touched.rePassword && (
                    <div className="label">
                      <span className="label-text-alt text-red-500">
                        {errors.rePassword + ''}
                      </span>
                    </div>
                  )}
                </label>

                <label htmlFor="phone" className="form-control mt-3">
                  <div className="label">
                    <span className="label-text">Phone</span>
                  </div>
                  <input
                    onChange={handleChange}
                    onBlur={handleBlur}
                    value={values.phone}
                    type="tel"
                    id="phone"
                    placeholder="ex: 01023456789"
                    className={
                      errors.phone && touched.phone
                        ? `input input-bordered input-error`
                        : `input input-bordered`
                    }
                  />
                  {errors.phone && touched.phone && (
                    <div className="label">
                      <span className="label-text-alt text-red-500">
                        {errors.phone + ''}
                      </span>
                    </div>
                  )}
                </label>
                <div className="flex items-center mt-5">
                  <Link to={'/login'} className="link link-primary me-auto">
                    Already a user? Sign-In
                  </Link>
                  <button className="btn btn-primary">
                    {loading ? (
                      <span className="loading loading-dots loading-lg"></span>
                    ) : (
                      'Submit'
                    )}
                  </button>
                </div>
              </div>
            </form>
          </div>
          {confirmMsg && (
            <div role="alert" className="alert alert-success">
              <svg
                xmlns="http://www.w3.org/2000/svg"
                className="stroke-current shrink-0 h-6 w-6"
                fill="none"
                viewBox="0 0 24 24"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth="2"
                  d="M9 12l2 2 4-4m6 2a9 9 0 11-18 0 9 9 0 0118 0z"
                />
              </svg>
              <span> {confirmMsg + ''} </span>
            </div>
          )}
          {errorMsg && (
            <div role="alert" className="alert alert-warning">
              <svg
                xmlns="http://www.w3.org/2000/svg"
                className="stroke-current shrink-0 h-6 w-6"
                fill="none"
                viewBox="0 0 24 24"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth="2"
                  d="M12 9v2m0 4h.01m-6.938 4h13.856c1.54 0 2.502-1.667 1.732-3L13.732 4c-.77-1.333-2.694-1.333-3.464 0L3.34 16c-.77 1.333.192 3 1.732 3z"
                />
              </svg>
              <span> {errorMsg + ''} </span>
            </div>
          )}
        </div>
      </div>
    </>
  );
}

export default Register;
