import React from 'react';
import { Link } from 'react-router-dom';

function EmptyOrders({ wishMsg, cartMsg }) {
  return (
    <div>
      <div className="flex flex-col items-center justify-center py-12 h-[90vh]">
        <svg
          xmlns="http://www.w3.org/2000/svg"
          width="1em"
          height="1em"
          viewBox="0 0 24 24"
          className="h-72 w-72 mb-4"
        >
          <g
            fill="none"
            stroke="currentColor"
            strokeLinecap="round"
            strokeWidth={1.5}
          >
            <path d="M12 21H4a2 2 0 0 1-2-2V5a2 2 0 0 1 2-2h16a2 2 0 0 1 2 2v8"></path>
            <path
              strokeLinejoin="round"
              d="M2 7h20M5 5.01l.01-.011M8 5.01l.01-.011M11 5.01l.01-.011M21 16.05a3.5 3.5 0 1 0-5 4.9m5-4.9a3.5 3.5 0 0 1-5 4.9m5-4.9l-5 4.9"
            ></path>
          </g>
        </svg>

        <p className="text-lg font-semibold mb-10">There are no orders.</p>
        <Link to="/products">
          <button className="py-2 btn btn-primary">Let's go shopping!</button>
        </Link>
      </div>
    </div>
  );
}

export default EmptyOrders;
