import React, { useEffect } from 'react';
import EmptyCart from './../EmptyCart/EmptyCart';
import Breadcrumbs from '../Breadcrumbs';
import { wishContext } from '../../Context/WishContext';
import WishItem from './WishItem';
import ErrorPage from '../ErrorPage';

function Wishlist() {
  let wishMsg = 'Your wishlist is empty.';
  let wishTitle = 'Wishlist';

  const { wishList, totalWishPrice, wishError } = React.useContext(wishContext);

  useEffect(() => {
    document.title = 'Wishlist - Ecommerce';
  });

  if (wishError) {
    return <ErrorPage error={wishError} />;
  }

  return (
    <div className="container mx-auto">
      <Breadcrumbs wishTitle={wishTitle} />
      <div className="grid grid-cols-10">
        <div className="col-span-10">
          <section className="py-6 sm:py-12">
            <div
              className={`mx-auto ${
                (wishList?.length <= 3 || !wishList) && 'md:h-[70vh]'
              } flex flex-col items-center justify-center max-w-screen-xl px-4 py-8 sm:px-6 sm:py-12 lg:px-8`}
            >
              <div className="mx-auto max-w-5xl">
                <ul className="space-y-4">
                  {(!wishList || wishList?.length > 0) && (
                    <h1 className="text-6xl text-center font-bold mb-3">
                      💖Wishlist
                    </h1>
                  )}
                  {wishList === null || wishList?.length === 0 ? (
                    <EmptyCart wishMsg={wishMsg} />
                  ) : (
                    wishList?.map((item, i) => (
                      <li
                        key={i}
                        className="w-[300px] sm:w-[500px] md:w-[650px] lg:w-[900px] flex flex-col sm:flex-row gap-3 items-center text-center sm:text-left border rounded-xl p-10"
                      >
                        <WishItem item={item} />
                      </li>
                    ))
                  )}
                </ul>
                <div className="flex flex-col md:items-center md:flex-row md:justify-between gap-3 mt-5">
                  <p
                    className={`text-xl font-bold sm:text-3xl ${
                      wishList === null || wishList?.length === 0
                        ? 'hidden'
                        : ''
                    }`}
                  >
                    Total:{' '}
                    <span className="text-primary">
                      {totalWishPrice?.toFixed(2)}
                    </span>{' '}
                    EGP
                  </p>
                </div>
              </div>
            </div>
          </section>
        </div>
      </div>
    </div>
  );
}

export default Wishlist;
