import { useFormik } from 'formik';
import * as Yup from 'yup';
import axios from 'axios';
import toast from 'react-hot-toast';
import { useNavigate } from 'react-router-dom';
import { useState } from 'react';

function ResetPassword() {
  const navigate = useNavigate();

  const [Loading, setLoading] = useState(false);

  const passRegex =
    /^(?=.*?[A-Z])(?=.*?[a-z])(?=.*?[0-9])(?=.*?[#?!@$ %^&*-]).{6,12}$/;

  const validationSchema = Yup.object({
    email: Yup.string().required('Email is required').email('Email is invalid'),
    newPassword: Yup.string()
      .required(`Password can't be empty`)
      .min(6, 'Password must be at least 6 characters')
      .max(12, 'Password must be at most 12 characters')
      .matches(
        passRegex,
        'Minimum 6 characters and Maximum 12 characters, at least one upper case English letter, one lower case English letter, one number and one special character'
      ),
  });

  const handleReset = async (body) => {
    setLoading(true);
    try {
      await axios.put(
        'https://ecommerce.routemisr.com/api/v1/auth/resetPassword',
        body
      );
      localStorage.setItem('token', null);
      toast.success('Password changed successfully', {
        duration: 3000,
        position: 'bottom-center',
        style: {
          border: '1px solid green',
          padding: '16px',
          color: 'black',
        },
      });
      setLoading(false);
      setTimeout(() => {
        navigate('/login');
      }, 3000);
    } catch (error) {
      setLoading(false);
      toast.error(error.response.data.message, {
        duration: 3000,
        position: 'bottom-center',
        style: {
          border: '1px solid red',
          padding: '16px',
          color: 'black',
        },
      });
    }
  };

  const formik = useFormik({
    initialValues: {
      email: '',
      newPassword: '',
    },
    validationSchema,
    onSubmit: (values) => {
      if (!values.email || !values.newPassword) return;
      handleReset(values);
    },
  });

  return (
    <>
      <form onSubmit={formik.handleSubmit} className="flex flex-col gap-3">
        <input
          type="email"
          placeholder="Email"
          className="input input-bordered"
          name="email"
          value={formik.values.email}
          onChange={formik.handleChange}
          onBlur={formik.handleBlur}
        />
        <input
          type="password"
          placeholder="New Password"
          className="input input-bordered"
          name="newPassword" // Change from "password" to "newPassword"
          value={formik.values.newPassword} // Change from "password" to "newPassword"
          onChange={formik.handleChange}
          onBlur={formik.handleBlur}
        />

        {formik.errors.email && formik.touched.email && (
          <div role="alert" className="alert alert-error">
            <svg
              xmlns="http://www.w3.org/2000/svg"
              className="stroke-current shrink-0 h-6 w-6"
              fill="none"
              viewBox="0 0 24 24"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth="2"
                d="M10 14l2-2m0 0l2-2m-2 2l-2-2m2 2l2 2m7-2a9 9 0 11-18 0 9 9 0 0118 0z"
              />
            </svg>
            <span>{formik.touched.email && formik.errors.email}</span>
          </div>
        )}

        {formik.errors.newPassword && formik.touched.newPassword && (
          <div role="alert" className="alert alert-error">
            <svg
              xmlns="http://www.w3.org/2000/svg"
              className="stroke-current shrink-0 h-6 w-6"
              fill="none"
              viewBox="0 0 24 24"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth="2"
                d="M10 14l2-2m0 0l2-2m-2 2l-2-2m2 2l2 2m7-2a9 9 0 11-18 0 9 9 0 0118 0z"
              />
            </svg>
            <span>
              {formik.touched.newPassword && formik.errors.newPassword}
            </span>
          </div>
        )}

        <button
          type="submit"
          disabled={!formik.isValid}
          className="btn btn-primary"
          onClick={formik.handleSubmit}
        >
          {Loading ? (
            <span className="loading loading-spinner"></span>
          ) : (
            'Reset'
          )}
        </button>
      </form>
    </>
  );
}

export default ResetPassword;
