import React from 'react';
import Slider from 'react-slick';

function HomePageSlider() {
  const settings = {
    dots: false,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
    autoplay: true,
    autoplaySpeed: 3000,
    arrows: false,
  };
  return (
    <div className="slider-container mt-10 overflow-hidden rounded-lg">
      <Slider {...settings}>
        <div className='rounded-lg overflow-hidden shadow-lg'>
          <img
            src={require('../../Assets/banner1.jpg')}
            alt="banner1"
            className="h-[250px] lg:h-[550px] md:h-[350px] sm:h-[450px] w-full rounded-lg "
          />
        </div>
        <div className='rounded-lg overflow-hidden shadow-lg'>
          <img
            src={require('../../Assets/banner2.jpg')}
            alt="banner2"
            className="h-[250px] lg:h-[550px] md:h-[350px] sm:h-[450px] w-full rounded-lg"
          />
        </div>
        <div className='rounded-lg overflow-hidden shadow-lg'>
          <img
            src={require('../../Assets/banner3.jpg')}
            alt="banner3"
            className="h-[250px] lg:h-[550px] md:h-[350px] sm:h-[450px] w-full rounded-lg"
          />
        </div>
      </Slider>
    </div>
  );
}

export default HomePageSlider;
