import axios from 'axios';

let token = localStorage.getItem('token');

export const CartClient = axios.create({
  baseURL: 'https://ecommerce.routemisr.com/api/v1/cart',
  headers: {
    Accept: '*/*',
    token,
  },
});
export const addToCartClient = axios.create({
  baseURL: `https://ecommerce.routemisr.com/api/v1/cart`,
  headers: {
    Accept: '*/*',
    token,
  },
});
