import React, { useContext, useEffect } from 'react';
import CartItem from './CartItem';
import { cartContext } from '../../Context/CartContext';
import CartSkeleton from './CartSkeleton';
import EmptyCart from '../EmptyCart/EmptyCart';
import CartBreadcrumbs from './CartBreadcrumbs';
import { Link } from 'react-router-dom';
import ErrorPage from './../ErrorPage';

function Cart() {
  const { cartItems, cartId, CartIsLoading, totalPrice, cartError } =
    useContext(cartContext);
  window.scrollTo({ top: 0, behavior: 'smooth' });

  let cartMsg = 'Your cart is empty.';

  useEffect(() => {
    document.title = 'Cart - Ecommerce';
  });

  if (cartError) {
    return <ErrorPage error={cartError} />;
  }

  return (
    <div className="container mx-auto">
      <CartBreadcrumbs />
      <div className="grid grid-cols-10">
        <div className="col-span-10">
          <section className="py-6 sm:py-12">
            <div
              className={`mx-auto ${
                (cartItems?.length <= 3 || !cartItems) && 'md:h-[70vh]'
              } flex flex-col items-center justify-center max-w-screen-xl px-4 py-8 sm:px-6 sm:py-12 lg:px-8`}
            >
              <div className="mx-auto max-w-5xl">
                <ul className="space-y-4">
                  {(!cartItems || cartItems?.length > 0) && (
                    <h1 className="text-6xl text-center font-bold mb-3">
                      🛒Cart
                    </h1>
                  )}
                  {CartIsLoading ? (
                    <>
                      <CartSkeleton />
                      <CartSkeleton />
                      <CartSkeleton />
                    </>
                  ) : cartItems === null || cartItems?.length === 0 ? (
                    <EmptyCart cartMsg={cartMsg} />
                  ) : (
                    cartItems?.map((cart, i) => (
                      <li
                        key={i}
                        className="w-[300px] sm:w-[450px] md:w-[650px] lg:w-[900px] flex flex-col md:flex-row gap-3 items-center text-center sm:text-left border rounded-xl p-10"
                      >
                        <CartItem cart={cart} />
                      </li>
                    ))
                  )}
                </ul>
                <div className="flex flex-col md:items-center md:flex-row md:justify-between gap-3 mt-5">
                  <p
                    className={`text-xl font-bold sm:text-3xl ${
                      cartItems === null || cartItems?.length === 0
                        ? 'hidden'
                        : ''
                    }`}
                  >
                    Total:{' '}
                    <span className="text-primary">
                      {totalPrice.toFixed(2)}
                    </span>{' '}
                    EGP
                  </p>
                  <Link to={`/checkout/${cartId}`}>
                    <button
                      className={`btn btn-primary ${
                        cartItems === null || cartItems?.length === 0
                          ? 'hidden'
                          : ''
                      }`}
                    >
                      Checkout
                    </button>
                  </Link>
                </div>
              </div>
            </div>
          </section>
        </div>
      </div>
    </div>
  );
}
export default Cart;
