import axios from 'axios';
import { useFormik } from 'formik';
import React, { useContext, useEffect, useState } from 'react';
import * as Yup from 'yup';
import { authContext } from '../../Context/AuthContext';

function ProfilePage() {
  const { token } = useContext(authContext);
  const [confirmMsg, setConfirmMsg] = useState(false);
  async function updateUser(body) {
    setLoading(true);
    try {
      const res = await axios.put(
        'https://ecommerce.routemisr.com/api/v1/users/updateMe/',
        body,
        {
          headers: {
            token,
          },
        }
      );
      const data = await res.data;
      setLoading(false);
      setConfirmMsg('Account updated successfuly!');
      setTimeout(() => {
        setConfirmMsg(false);
      }, 3000);
      localStorage.setItem('userName', data.user.name);
    } catch (err) {
      setLoading(false);
      setErrorMsg(err.response.data.errors.msg);
      setTimeout(() => {
        setErrorMsg(false);
      }, 3000);
    }
  }
  const [loading, setLoading] = useState(false);
  const [errorMsg, setErrorMsg] = useState(false);
  const phoneRegex = /^(010|011|012|015)[0-9]{8}$/;

  const validationSchema = Yup.object({
    name: Yup.string()
      .required('Name is required')
      .min(3, 'Name must atleast has 3 characters')
      .max(15, 'Name must be atmost 15 characters'),
    email: Yup.string()
      .email('Invalid email address')
      .required('Email is required'),
    phone: Yup.string()
      .matches(
        phoneRegex,
        'Egyptian mobile phone must start with  (010, 011, 012, or 015) followed by 8 digits'
      )
      .required('Phone is required'),
  });

  function onSubmit(values) {
    updateUser(values);
  }

  const formik = useFormik({
    initialValues: {
      name: '',
      email: '',
      phone: '',
    },
    onSubmit,
    validationSchema,
  });

  useEffect(() => {
    document.title = 'Profile - Ecommerce';
  });

  return (
    <div className="container mx-auto my-20 px-4">
      {/*  */}

      <div className="mockup-window border bg-base-300">
        <div className="flex flex-col items-center justify-center px-4 py-16 bg-base-200">
          <div className="avatar mb-5">
            <div className="size-40 rounded-full">
              <img
                src="https://yt3.googleusercontent.com/_o5DzVky5nU6LlOlRIqYiqQ_zd5L3ek04TzXPbJnsl2-iGwneCwZevBj5dZf157ZP8cy6xDDP9I=s900-c-k-c0x00ffffff-no-rj"
                alt="profile"
              />
            </div>
          </div>
          <h3 className="text-3xl font-bold">
            {localStorage.getItem('userName')}
          </h3>

          <div className="mt-10 w-full">
            <h3 className="text-2xl text-center font-bold capitalize mb-3">
              update personal data:
            </h3>

            <form
              onSubmit={formik.handleSubmit}
              className="flex flex-col gap-4 max-w-lg mx-auto"
            >
              <label htmlFor="name">
                <input
                  id="name"
                  type="text"
                  placeholder="New Name Here"
                  onChange={formik.handleChange}
                  onBlur={formik.handleBlur}
                  value={formik.values.name}
                  className={
                    formik.errors.name && formik.touched.name
                      ? `input input-bordered input-error  w-full `
                      : `input input-bordered w-full `
                  }
                />
                {formik.errors.name && formik.touched.name && (
                  <div className="label">
                    <span className="label-text-alt text-red-500">
                      {formik.errors.name + ''}
                    </span>
                  </div>
                )}
              </label>
              <label htmlFor="email">
                <input
                  id="email"
                  type="email"
                  placeholder="New Email Here"
                  className={
                    formik.errors.email && formik.touched.email
                      ? `input input-bordered input-error  w-full `
                      : `input input-bordered w-full `
                  }
                  onChange={formik.handleChange}
                  onBlur={formik.handleBlur}
                  value={formik.values.email}
                />
                {formik.errors.email && formik.touched.email && (
                  <div className="label">
                    <span className="label-text-alt text-red-500">
                      {formik.errors.email + ''}
                    </span>
                  </div>
                )}
              </label>
              <label htmlFor="phone">
                <input
                  id="phone"
                  type="tel"
                  placeholder="New Phone Here"
                  className={
                    formik.errors.phone && formik.touched.phone
                      ? `input input-bordered input-error  w-full `
                      : `input input-bordered w-full `
                  }
                  onChange={formik.handleChange}
                  onBlur={formik.handleBlur}
                  value={formik.values.phone}
                />
                {formik.errors.phone && formik.touched.phone && (
                  <div className="label">
                    <span className="label-text-alt text-red-500">
                      {formik.errors.phone + ''}
                    </span>
                  </div>
                )}
              </label>
              <button type="submit" className="btn btn-primary">
                {loading ? (
                  <span className="loading loading-dots loading-lg"></span>
                ) : (
                  'Submit'
                )}
              </button>
              {errorMsg && (
                <div role="alert" className="alert alert-warning mt-5">
                  <svg
                    xmlns="http://www.w3.org/2000/svg"
                    className="stroke-current shrink-0 h-6 w-6"
                    fill="none"
                    viewBox="0 0 24 24"
                  >
                    <path
                      strokeLinecap="round"
                      strokeLinejoin="round"
                      strokeWidth="2"
                      d="M12 9v2m0 4h.01m-6.938 4h13.856c1.54 0 2.502-1.667 1.732-3L13.732 4c-.77-1.333-2.694-1.333-3.464 0L3.34 16c-.77 1.333.192 3 1.732 3z"
                    />
                  </svg>
                  <span> {errorMsg + ''} </span>
                </div>
              )}
              {confirmMsg && (
                <div role="alert" className="alert alert-success mt-5">
                  <svg
                    xmlns="http://www.w3.org/2000/svg"
                    className="stroke-current shrink-0 h-6 w-6"
                    fill="none"
                    viewBox="0 0 24 24"
                  >
                    <path
                      strokeLinecap="round"
                      strokeLinejoin="round"
                      strokeWidth="2"
                      d="M9 12l2 2 4-4m6 2a9 9 0 11-18 0 9 9 0 0118 0z"
                    />
                  </svg>
                  <span> {confirmMsg + ''} </span>
                </div>
              )}
            </form>
          </div>
        </div>
      </div>
    </div>
  );
}

export default ProfilePage;
