import React, { useEffect } from 'react';
import { OrdersClient } from './../../APIs/OrdersApi';
import { useQuery } from '@tanstack/react-query';
import PageLoader from './../PageLoader';
import EmptyOrders from '../EmptyCart/EmptyOrders';
import ErrorPage from '../ErrorPage';

function Orders() {
  const userid = localStorage.getItem('userID');

  // const { getCartItems } = React.useContext(cartContext);

  const ordersFetch = () => {
    if (!userid) return;
    return OrdersClient.get('/' + userid);
  };

  const { data, isError, isLoading, error } = useQuery({
    queryKey: ['orders'],
    queryFn: ordersFetch,
  });

  let orders = data?.data;

  useEffect(() => {
    document.title = 'Orders - Ecommerce';
  });

  // Function to convert date format
  function formatDate(dateString) {
    const date = new Date(dateString);
    const day = date.getDate().toString().padStart(2, '0');
    const month = (date.getMonth() + 1).toString().padStart(2, '0');
    const year = date.getFullYear();

    return `${day}/${month}/${year}`;
  }

  if (isLoading) {
    return <PageLoader />;
  }

  if (isError) {
    return <ErrorPage error={error.message} />;
  }


  return (
    <div
      className={`container mx-auto mt-10 p-5 ${
        orders?.length <= 10 && 'h-[80vh]'
      }`}
    >
      {(orders?.length > 0 || !orders) && (
        <h1 className="text-6xl text-center font-bold mb-10">
          📃Orders
        </h1>
      )}
      <div className="overflow-x-auto">
        {orders?.length > 0 ? (
          <table className="table table-sm table-zebra">
            <thead>
              <tr>
                <th></th>
                <th>Name</th>
                <th>Phone</th>
                <th>Email</th>
                <th>Location</th>
                <th>Order Count</th>
                <th>Total Price</th>
                <th>Payment Method</th>
                <th>Date</th>
              </tr>
            </thead>
            <tbody>
              {orders?.map((order, i) => (
                <tr key={i}>
                  <th>{i + 1}</th>
                  <td>{order.user.name}</td>
                  <td>{order.user.phone}</td>
                  <td>{order.user.email}</td>
                  <td>{order.shippingAddress.city}</td>
                  <td>{order.cartItems.length}</td>
                  <td>{order.totalOrderPrice} EGP</td>
                  <td className="uppercase">{order.paymentMethodType}</td>
                  <td>{formatDate(order.createdAt)}</td>
                </tr>
              ))}
            </tbody>
          </table>
        ) : (
          <EmptyOrders />
        )}
      </div>
    </div>
  );
}

export default Orders;
