import { useFormik } from 'formik';
import React, { useEffect } from 'react';
import * as Yup from 'yup';
import { CheckoutClient } from '../../APIs/CheckoutApi';
import { useParams } from 'react-router-dom';
import toast from 'react-hot-toast';
function Checkout() {
  const [loading, setLoading] = React.useState(false);

  const { cartid } = useParams();

  const phoneRegex = /^(010|011|012|015)[0-9]{8}$/;

  const validationSchema = Yup.object({
    phone: Yup.string()
      .matches(
        phoneRegex,
        'Phone number must start with 010, 011, 012, or 015 followed by 8 digits'
      )
      .required('Phone is required'),
    city: Yup.string().required('City is required'),
    details: Yup.string().required('Details is required'),
  });

  function sendUserData(body, id) {
    setLoading(true);
    CheckoutClient.post(`/${id}`, {
      'shippingAddress': body,
    })
      .then((res) => {
        toast.success(`You'll be redirected to payment page after 3 seconds!`, {
          position: 'bottom-center',
          duration: 3000,
          style: {
            border: '1px solid green',
            padding: '16px',
            color: 'black',
          },
        });
        setTimeout(() => {
          window.open(res.data.session.url, '_self');
        }, 3000);
        setLoading(false);
      })
      .catch((err) => {

        toast.error(`${err.message}`, {
          position: 'bottom-center',
          duration: 3000,
          style: {
            border: '1px solid red',
            padding: '16px',
            color: 'black',
          },
        });
        setLoading(false);
      });
  }

  const onSubmit = () => {
    sendUserData(formik.values, cartid);

  };

  const formik = useFormik({
    validationSchema,
    onSubmit,
    initialValues: {
      phone: '',
      city: '',
      details: '',
    },
  });

  useEffect(() => {
    document.title = 'Checkout';
  })

  return (
    <div className={`container mx-auto min-h-[80vh] flex flex-col items-center justify-center gap-4`}>
      <h1>Checkout</h1>
      <form
        onSubmit={formik.handleSubmit}
        className="md:w-1/2 w-4/5 flex flex-col gap-4"
      >
        <label
          htmlFor="phone"
          className={`input input-bordered ${
            formik.errors.phone && formik.touched.phone ? 'input-error' : ''
          } flex items-center gap-2`}
        >
          <span>
            <svg
              className="w-6 h-6 text-primary"
              aria-hidden="true"
              xmlns="http://www.w3.org/2000/svg"
              width={24}
              height={24}
              fill="none"
              viewBox="0 0 24 24"
            >
              <path
                stroke="currentColor"
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth={2}
                d="M18.427 14.768 17.2 13.542a1.733 1.733 0 0 0-2.45 0l-.613.613a1.732 1.732 0 0 1-2.45 0l-1.838-1.84a1.735 1.735 0 0 1 0-2.452l.612-.613a1.735 1.735 0 0 0 0-2.452L9.237 5.572a1.6 1.6 0 0 0-2.45 0c-3.223 3.2-1.702 6.896 1.519 10.117 3.22 3.221 6.914 4.745 10.12 1.535a1.601 1.601 0 0 0 0-2.456Z"
              />
            </svg>
          </span>
          <input
            name="phone"
            id="phone"
            type="phone"
            className="grow overflow-hidden"
            placeholder="Enter your phone"
            onChange={formik.handleChange}
            onBlur={formik.handleBlur}
            value={formik.values.phone}
          />
        </label>
        <label
          htmlFor="city"
          className={`input input-bordered ${
            formik.errors.city && formik.touched.city ? 'input-error' : ''
          } flex items-center gap-2`}
        >
          <span>
            <svg
              className="w-6 h-6 text-primary"
              aria-hidden="true"
              xmlns="http://www.w3.org/2000/svg"
              width={24}
              height={24}
              fill="none"
              viewBox="0 0 24 24"
            >
              <path
                stroke="currentColor"
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth={2}
                d="m4 12 8-8 8 8M6 10.5V19a1 1 0 0 0 1 1h3v-3a1 1 0 0 1 1-1h2a1 1 0 0 1 1 1v3h3a1 1 0 0 0 1-1v-8.5"
              />
            </svg>
          </span>

          <input
            name="city"
            id="city"
            type="text"
            className="grow overflow-hidden"
            placeholder="Enter your address"
            onChange={formik.handleChange}
            onBlur={formik.handleBlur}
            value={formik.values.city}
          />
        </label>

        <textarea
          className={`textarea textarea-bordered ${
            formik.errors.details && formik.touched.details
              ? 'textarea-error'
              : ''
          }`}
          placeholder="Enter extra order details"
          onChange={formik.handleChange}
          onBlur={formik.handleBlur}
          value={formik.values.details}
          name="details"
          id="details"
        ></textarea>
        <button
          type="submit"
          disabled={!formik.isValid || loading || formik.isSubmitting}
          className="btn btn-primary"
          onClick={() => {
            formik.handleSubmit();
 
          }}
        >
          {loading ? (
            <span className="loading loading-spinner loading-md"></span>
          ) : (
            'Submit'
          )}
        </button>
      </form>
    </div>
  );
}

export default Checkout;
