import React from 'react';
import { Link } from 'react-router-dom';
import AddProductButton from './AddProductButton';
import { wishContext } from '../../Context/WishContext';
import AddWishButton from './AddWishButton';

function SingleProduct({ product }) {
  const { addToWishList } = React.useContext(wishContext);

  return (
    <div className="group relative rounded-lg block overflow-hidden">
      {product.priceAfterDiscount > 0 && (
        <div className="absolute border-0 start-4 top-4 z-10 badge bg-red-500 p-3 text-white animate-pulse">
          <div className="">
            <span className="text-base font-medium uppercase tracking-wider text-white">
              Sale
            </span>
          </div>
        </div>
      )}
      <figure className="img relative overflow-hidden border-t border-l border-r rounded-t-lg ">
        <img
          src={product.imageCover}
          alt=""
          className="h-64 w-full object-cover transition duration-300 group-hover:scale-105 sm:h-72"
        />
        <div className="absolute end-4 top-4 z-10 opacity-0 group-hover:opacity-100">
          <div
            className="tooltip tooltip-primary tooltip-left"
            data-tip={
              localStorage.getItem('wishList').includes(product.id)
                ? 'Already in wishlist'
                : 'Add to wishlist'
            }
          >
            <AddWishButton product={product} addToWishList={addToWishList} />
          </div>
        </div>
        <Link to={`/product/${product.slug}/${product.id}`}>
          <button className="absolute top-80 opacity-0 group-hover:opacity-100 group-hover:top-[14rem] sm:group-hover:top-[16rem] left-1/2 transform -translate-x-1/2 -translate-y-1/2 z-10 rounded px-5 bg-primary p-1.5 text-white transition duration-300">
            Details
          </button>
        </Link>
      </figure>

      <div className="relative border rounded-b-lg bg-white p-6">
        <span className="whitespace-nowrap rounded bg-slate-500 px-3 py-1.5 text-white text-sm uppercase font-medium">
          {product.brand.name}
        </span>

        <h3 className="mt-4 line-clamp-1 text-lg font-medium text-gray-900">
          {product.title}
        </h3>
        <p className="mt-1.5 text-sm text-gray-700">{product.price} EGP</p>

        <AddProductButton product={product} />
      </div>
    </div>
  );
}

export default SingleProduct;
