import React from 'react';
import Slider from 'react-slick';
import { ProductsClient } from './../../APIs/ProductsApi';
import { useQuery } from '@tanstack/react-query';
import { Link } from 'react-router-dom';

function PopularSlider() {
  const settings = {
    dots: false,
    infinite: true,
    speed: 500,
    slidesToShow: 6,
    slidesToScroll: 1,
    arrows: false,
    autoplay: true,
    autoplaySpeed: 3000,
    responsive: [
      {
        breakpoint: 3000,
        settings: {
          slidesToShow: 6,
          slidesToScroll: 1,
        },
      },
      {
        breakpoint: 1280,
        settings: {
          slidesToShow: 4,
          slidesToScroll: 4,
        },
      },
      {
        breakpoint: 992,
        settings: {
          slidesToShow: 3,
          slidesToScroll: 3,
        },
      },
      {
        breakpoint: 540,
        settings: {
          slidesToShow: 2,
          slidesToScroll: 2,
        },
      },
      {
        breakpoint: 400,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
        },
      },
    ],
  };

  function getPopular() {
    return ProductsClient.get('?limit=10&page=5');
  }

  const { data, isLoading } = useQuery({
    queryKey: ['popularProducts'],
    queryFn: () => getPopular(),
  });

  let Products = data?.data?.data;

  if (isLoading) {
    return (
      <div className="flex justify-center gap-5 my-10">
        <div>
          <div className="skeleton w-32 h-32 rounded-full"></div>
          <div className="skeleton w-32 h-5 rounded-full mt-2"></div>
        </div>
        <div>
          <div className="skeleton w-32 h-32 rounded-full"></div>
          <div className="skeleton w-32 h-5 rounded-full mt-2"></div>
        </div>
        <div>
          <div className="skeleton w-32 h-32 rounded-full"></div>
          <div className="skeleton w-32 h-5 rounded-full mt-2"></div>
        </div>
        <div>
          <div className="skeleton w-32 h-32 rounded-full"></div>
          <div className="skeleton w-32 h-5 rounded-full mt-2"></div>
        </div>
        <div>
          <div className="skeleton w-32 h-32 rounded-full"></div>
          <div className="skeleton w-32 h-5 rounded-full mt-2"></div>
        </div>
      </div>
    );
  }

  return (
    <div className="slider-container my-10">
      <Slider {...settings}>
        {Products?.map((product, index) => (
          <div
            key={index}
            className="hover:scale-105 py-2 transition duration-300"
          >
            <Link to={`/product/${product?.slug}/${product?._id}`}>
              <figure>
                <img
                  src={product.imageCover}
                  alt={product.slug}
                  className="w-full h-full object-center mx-auto object-contain rounded-full shadow-lg"
                  style={{ height: '200px', width: '200px' }}
                />
              </figure>
            </Link>
            <p className="text-center text-lg font-semibold mt-2">
              {product.title.split(' ').slice(0, 2).join(' ')}
            </p>
          </div>
        ))}
      </Slider>
    </div>
  );
}

export default PopularSlider;
