import React from 'react';
import { useParams } from 'react-router-dom';
import { useQuery } from '@tanstack/react-query';
import { ProductsClient } from '../../APIs/ProductsApi';
import PageLoader from '../PageLoader';
import NotFound from '../NotFound';
import Slider from 'react-slick';
import Reviews from '../Reviews/Reviews';
import AddProductButton from './AddProductButton';
import Breadcrumbs from '../Breadcrumbs';
import AddWishButton from './AddWishButton';

function ProductDetails() {
  window.scrollTo(0, 0);
  const params = useParams();

  function getProductDetails() {
    return ProductsClient.get(`/${params.id}`);
  }

  const {
    data: response,
    isLoading,
    isError,
    isFetching,
  } = useQuery({
    queryKey: ['productDetails'],
    queryFn: getProductDetails,
  });

  let product = response?.data.data;

  function duplicate() {
    if (product?.title === 'PS5 DualSense Charging Station') {
      return Array.from({ length: 4 }, () => product.images[0]);
    }
  }

  const ps5Images = duplicate();

  const settings = {
    customPaging: function (i) {
      return (
        <div>
          <img
            src={`
          ${
            product.title === 'PS5 DualSense Charging Station'
              ? ps5Images[i]
              : product.images[i]
          }
          `}
            alt={
              product.title === 'PS5 DualSense Charging Station'
                ? 'PS5'
                : product.slug[i]
            }
          />
        </div>
      );
    },
    dots: true,
    dotsClass: 'slick-dots slick-thumb',
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
    arrows: false,
  };

  if (isLoading || response === null || isFetching) {
    return <PageLoader />;
  }

  if (isError) {
    return <NotFound />;
  }

  return (
    <div className="container mx-auto px-5">
      <Breadcrumbs product={product} />
      {/* product details */}
      <div className="flex flex-col md:grid md:grid-cols-10 gap-y-10 md:gap-x-5 my-5">
        <div className="md:row-span-4 md:col-span-4 rounded-lg">
          <div className="slider-container">
            <Slider {...settings}>
              {product.title === 'PS5 DualSense Charging Station'
                ? ps5Images.map((img, i) => {
                    return (
                      <figure key={i}>
                        <img
                          src={`${img}`}
                          alt={product.slug}
                          className="m-auto rounded-lg lg:h-[450px] md:h-[350px] sm:h-[450px] mb-3"
                        />
                      </figure>
                    );
                  })
                : product.images.map((img, i) => {
                    return (
                      <figure key={i}>
                        <img
                          src={`${img}`}
                          alt={product.slug}
                          className="m-auto rounded-lg lg:h-[450px] md:h-[350px] sm:h-[450px] mb-3"
                        />
                      </figure>
                    );
                  })}
            </Slider>
          </div>
        </div>

        <div className="md:row-span-6 md:col-span-6">
          <div className="flex gap-3">
            <h1 className="text-3xl font-bold">{product.title}</h1>

            <div
              className="tooltip tooltip-primary"
              data-tip={
                localStorage.getItem('wishList').includes(product.id)
                  ? 'Already in wishlist'
                  : 'Add to wishlist'
              }
            >
              <AddWishButton product={product} />
            </div>
          </div>

          {/* brand and category */}
          <div className="flex gap-1 mt-3">
            <p className="text-white p-3 badge bg-primary">
              {product.brand.name}
            </p>
            <p className="text-white p-3 badge bg-sky-600">
              {product.category.name}
            </p>
          </div>
          <p className="text-gray-400 my-5 text-sm md:text-base">
            <span className="font-bold">Rating:</span> {product.ratingsAverage}{' '}
            / 5 ⭐
          </p>

          {/* discription */}
          <p className="text-gray-400 my-5 text-sm md:text-base first-letter:capitalize">
            {product.description}
          </p>
          {/* {/* price} */}
          <div className="text-primary font-bold my-5 text-3xl">
            {product.priceAfterDiscount ? (
              <div>
                <span className="line-through text-gray-400 text-base">
                  {product.price} EGP
                </span>
                <span> {product.priceAfterDiscount} EGP</span>
              </div>
            ) : (
              product.price + ' EGP'
            )}
          </div>
          {/* add to cart button */}
          <AddProductButton product={product} />
        </div>
      </div>

      {/* reviews */}
      <Reviews product={product} />
    </div>
  );
}

export default ProductDetails;
