import React from 'react';
import { WishlistClient } from '../APIs/WishlistApi';
import { authContext } from './AuthContext';
import toast from 'react-hot-toast';

export const wishContext = React.createContext();

function WishContextProvider(props) {
  const { token } = React.useContext(authContext);
  const [wishList, setWishList] = React.useState(null);
  const [numOfWishItems, setNumOfWishItems] = React.useState(0);
  const [addWishResponse, setAddWishResponse] = React.useState([]);
  const [wishError, setWishError] = React.useState(null);

  const getWishList = () => {
    WishlistClient.get()
      .then((res) => {
        localStorage.setItem(
          'wishList',
          JSON.stringify(res.data.data.map((item) => item._id))
        );
        setWishList(res.data.data);
        setNumOfWishItems(res.data.data.length);
      })
      .catch((err) => {
        setWishError(err.message);
      });
  };

  const addToWishList = async (productId) => {
    const response = await WishlistClient.post('', {
      productId: productId,
    })
      .then((res) => {
        toast.success('Added Successfully', {
          position: 'bottom-center',
          duration: 3000,
          style: {
            border: '1px solid #713200',
            padding: '16px',
            color: '#713200',
          },
        });
        setWishList(res.data.data);
        setNumOfWishItems(res.data.data.length);
        setAddWishResponse(res.data.data);
        localStorage.setItem('wishList', JSON.stringify(res.data.data));
        getWishList();
        return true;
      })
      .catch((err) => {
        toast.error(`${err.message}`, {
          position: 'bottom-center',
          duration: 3000,
          style: {
            border: '1px solid red',
            padding: '16px',
            color: 'black',
          },
        });
        return false;
      });
    return response;
  };

  const deleteFromWishList = async (productId) => {
    const response = await WishlistClient.delete(`/${productId}`)
      .then((res) => {
        setNumOfWishItems(res.data.data.length);
        localStorage.setItem('wishList', JSON.stringify(res.data.data));
        getWishList();
        return true;
      })
      .catch((err) => {
        toast.error(`WishListApi: ${err.message}`, {
          position: 'bottom-center',
          duration: 3000,
          style: {
            border: '1px solid red',
            padding: '16px',
            color: 'black',
          },
        });
        return false;
      });
    return response;
  };

  const totalWishPrice = wishList?.reduce(
    (accumulator, currentItem) => accumulator + currentItem.price,
    0
  );

  React.useEffect(() => {
    if (!token) {
      return;
    } else {
      getWishList();
    }
  }, [token]);

  return (
    <>
      <wishContext.Provider
        value={{
          wishList,
          totalWishPrice,
          numOfWishItems,
          addToWishList,
          deleteFromWishList,
          addWishResponse,
          wishError,
        }}
      >
        {props.children}
      </wishContext.Provider>
    </>
  );
}

export default WishContextProvider;
