import axios from 'axios';

const url = 'https://ecommerce.routemisr.com/api/v1/orders/checkout-session/';
const token = localStorage.getItem('token');

export const CheckoutClient = axios.create({
  baseURL: url,
  headers: {
    Accept: '*/*',
    'token': token,
  },
  params: {
    url: window.location.origin,
  },
});
