import React from 'react'
import { Link } from 'react-router-dom';

function CartBreadcrumbs() {
  return (
    <div>
      <div className="text-sm breadcrumbs mt-5 ms-5">
        <ul>
          <li>
            <Link to="/" className="link-primary">
              Home
            </Link>
          </li>
          <li>
            <Link to="/products" className="link-primary">
              Products
            </Link>
          </li>
          <li>
            <span>Cart</span>
          </li>
        </ul>
      </div>
    </div>
  );
}

export default CartBreadcrumbs