import React from 'react';

function Accordion() {
  return (
    <div className="w-full flex flex-col gap-5 mt-10">
      <div
        tabIndex={0}
        className="collapse collapse-arrow border border-base-300 bg-base-200"
      >
        <div className="collapse-title text-xl font-medium">
          What is the difference between authentication vs authorization?
        </div>
        <div className="collapse-content">
          <p>
            Authentication is the process of verifying who you are.
            Authorization is the process of verifying what specific
            applications, files, and data you have access to.
          </p>
        </div>
      </div>
      <div
        tabIndex={0}
        className="collapse collapse-arrow border border-base-300 bg-base-200"
      >
        <div className="collapse-title text-xl font-medium">
          What is the SEO principle in web development?
        </div>
        <div className="collapse-content">
          <p>
            Search Engine Optimization (SEO) is a set of practices that helps
            improve the quality and quantity of website traffic to a website.
            SEO helps to increase the ranking of a website in search engines.
          </p>
        </div>
      </div>
      <div
        tabIndex={0}
        className="collapse collapse-arrow border border-base-300 bg-base-200"
      >
        <div className="collapse-title text-xl font-medium">
          What is the SPA principle in web development?
        </div>
        <div className="collapse-content">
          <p>
            Single Page Application (SPA) is a type of web application that
            allows users to navigate through multiple web pages without
            reloading the whole page.
          </p>
        </div>
      </div>
      <div
        tabIndex={0}
        className="collapse collapse-arrow border border-base-300 bg-base-200"
      >
        <div className="collapse-title text-xl font-medium">
          What is the React lifecycle in web development?
        </div>
        <div className="collapse-content">
          <p>
            React lifecycle refers to the sequence of phases that a React
            component undergoes during its lifetime. These phases are: Mounting,
            Updating, and Unmounting. Each phase is responsible for different
            tasks. React lifecycle is a crucial part of React development.
          </p>
        </div>
      </div>
      <div
        tabIndex={0}
        className="collapse collapse-arrow border border-base-300 bg-base-200"
      >
        <div className="collapse-title text-xl font-medium">
          What is the Admin dashboard in web development?
        </div>
        <div className="collapse-content">
          <p>
            An admin dashboard is a type of web application that allows
            administrators to manage and monitor the activities of a website.
          </p>
        </div>
      </div>
    </div>
  );
}

export default Accordion;
