import axios from 'axios';
import { useFormik } from 'formik';
import React, { useContext, useEffect, useState } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import * as Yup from 'yup';
import { authContext } from '../../Context/AuthContext';
import styles from './Login.module.css'

function Login() {
  window.scrollTo({ top: 0, behavior: 'smooth' });
  const [errorMsg, setErrorMsg] = useState(false);
  const [confirmMsg, setConfirmMsg] = useState(false);
  const [loading, setLoading] = useState(false);
  const { setToken } = useContext(authContext);

  const passRegex =
    /^(?=.*?[A-Z])(?=.*?[a-z])(?=.*?[0-9])(?=.*?[#?!@$ %^&*-]).{6,12}$/;

  const initialValues = {
    'email': '',
    'password': '',
  };

  const validationSchema = Yup.object({
    'email': Yup.string()
      .email('Email is not valid.')
      .required('Email is required.'),
    'password': Yup.string()
      .required(`Password can't be empty`)
      .min(6, 'Password must be atleast 6 characters')
      .max(12, 'Password must be atmost 12 characters')
      .matches(
        passRegex,
        'Minimum 6 characters and Maximum 12 characters, at least one upper case English letter, one lower case English letter, one number and one special character'
      ),
  });

  const navigate = useNavigate();



  const sendUserData = (body) => {
    setLoading(true);
    axios
      .post('https://ecommerce.routemisr.com/api/v1/auth/signin', body)
      .then(({ data }) => {
        if (data.message === 'success') {
          setLoading(false);
          setToken(data.token);
          localStorage.setItem('token', data.token);
          localStorage.setItem('userName', data.user.name);
          setConfirmMsg(`Welcome back, ${data.user.name}! 🎉🥳`);
          setTimeout(() => {
            setConfirmMsg(false);
            navigate('/');
          }, 2000);
        }
      })
      .catch((error) => {
        setLoading(false);
        setErrorMsg(error.response.data.message);
        setTimeout(() => {
          setErrorMsg(false);
        }, 3000);
      });
  };

  const onSubmit = (values) => {
    sendUserData(values);
  };

  const { handleSubmit, handleChange, handleBlur, touched, errors, values } =
    useFormik({
      initialValues,
      validationSchema,
      onSubmit,
    });

    useEffect(() => {
      document.title = 'Login - Ecommerce'
    })

  return (
    <>
      <div className={`${styles.bgImage} hero min-h-screen bg-base-50`}>
        <div className="hero-content flex-col items-center justify-center">
      
          <div className="text-center grid grid-cols-1">
            <div className="card shrink-0 w-full md:w-[410px] lg:w-[620px] shadow-2xl bg-base-100 mt-10">
              <form onSubmit={handleSubmit} className="card-body">
                <div className="form-control flex gap-4">
                  <label
                    htmlFor="email"
                    className={
                      errors.email && touched.email
                        ? `flex items-center gap-2 input input-bordered input-error`
                        : `flex items-center gap-2 input input-bordered`
                    }
                  >
                    <svg
                      xmlns="http://www.w3.org/2000/svg"
                      viewBox="0 0 16 16"
                      fill="currentColor"
                      className="w-4 h-4 opacity-70"
                    >
                      <path d="M8 8a3 3 0 1 0 0-6 3 3 0 0 0 0 6ZM12.735 14c.618 0 1.093-.561.872-1.139a6.002 6.002 0 0 0-11.215 0c-.22.578.254 1.139.872 1.139h9.47Z" />
                    </svg>
                    <input
                      id="email"
                      type="text"
                      className="grow"
                      onBlur={handleBlur}
                      onChange={handleChange}
                      placeholder="Email"
                      value={values.email}
                    />
                  </label>
                  {errors.email && touched.email && (
                    <div className="label mt-[-15px]">
                      <span className="label-text-alt text-red-500">
                        {errors.email + ''}
                      </span>
                    </div>
                  )}
                  <label
                    htmlFor="password"
                    className={
                      errors.password && touched.password
                        ? `flex items-center gap-2 input input-bordered input-error`
                        : `flex items-center gap-2 input input-bordered`
                    }
                  >
                    <svg
                      xmlns="http://www.w3.org/2000/svg"
                      viewBox="0 0 16 16"
                      fill="currentColor"
                      className="w-4 h-4 opacity-70"
                    >
                      <path
                        fillRule="evenodd"
                        d="M14 6a4 4 0 0 1-4.899 3.899l-1.955 1.955a.5.5 0 0 1-.353.146H5v1.5a.5.5 0 0 1-.5.5h-2a.5.5 0 0 1-.5-.5v-2.293a.5.5 0 0 1 .146-.353l3.955-3.955A4 4 0 1 1 14 6Zm-4-2a.75.75 0 0 0 0 1.5.5.5 0 0 1 .5.5.75.75 0 0 0 1.5 0 2 2 0 0 0-2-2Z"
                        clipRule="evenodd"
                      />
                    </svg>
                    <input
                      id="password"
                      type="password"
                      className="grow"
                      onBlur={handleBlur}
                      onChange={handleChange}
                      placeholder="Password"
                      value={values.password}
                    />
                  </label>
                  {errors.password && touched.password && (
                    <div className="label mt-[-15px]">
                      <span className="label-text-alt text-red-500">
                        {errors.password + ''}
                      </span>
                    </div>
                  )}
                  <div className="flex justify-between">
                    <div className="text-left">
                      <Link to={'/forget-password'} className="link link-primary">
                        Forget Password?
                      </Link>
                    </div>
                    <div className="text-center">
                      <Link to={'/register'} className="link link-primary">
                        Register
                      </Link>
                    </div>
                  </div>
                  <button type="submit" className="btn btn-primary">
                    {loading ? (
                      <span className="loading loading-dots loading-lg"></span>
                    ) : (
                      'Login'
                    )}
                  </button>
                </div>
              </form>
            </div>
          </div>
          {confirmMsg && (
            <div role="alert" className="alert alert-success">
              <svg
                xmlns="http://www.w3.org/2000/svg"
                className="stroke-current shrink-0 h-6 w-6"
                fill="none"
                viewBox="0 0 24 24"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth="2"
                  d="M9 12l2 2 4-4m6 2a9 9 0 11-18 0 9 9 0 0118 0z"
                />
              </svg>
              <span> {confirmMsg + ''} </span>
            </div>
          )}
          {errorMsg && (
            <div role="alert" className="alert alert-warning">
              <svg
                xmlns="http://www.w3.org/2000/svg"
                className="stroke-current shrink-0 h-6 w-6"
                fill="none"
                viewBox="0 0 24 24"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth="2"
                  d="M12 9v2m0 4h.01m-6.938 4h13.856c1.54 0 2.502-1.667 1.732-3L13.732 4c-.77-1.333-2.694-1.333-3.464 0L3.34 16c-.77 1.333.192 3 1.732 3z"
                />
              </svg>
              <span> {errorMsg + ''} </span>
            </div>
          )}
        </div>
      </div>
    </>
  );
}

export default Login;
