import React from 'react';

function Hero() {
  function handleClick() {
    const div = document.getElementById('startExplore');
    const y = div.getBoundingClientRect().top - 235;
    window.scrollTo({ top: y, behavior: 'smooth' });
  }

  return (
    <section>
      <div className="mx-auto min-h-[50vh] items-center flex justify-center px-4 py-8 sm:px-6 sm:py-12 lg:px-8">
        <div className="mx-auto text-center">
          <h1 className="text-3xl text-primary font-extrabold md:text-5xl lg:text-6xl">
            FreshCart Ecommerce
          </h1>

          <p className="mx-auto mt-4 max-w-xl sm:text-xl/relaxed">
            Join the biggest online shopping community in Egypt, and start your
            shopping journey now.
          </p>

          <div className="mt-8 flex flex-wrap justify-center gap-4">
            <button onClick={handleClick} className="btn btn-primary px-12 py-3">Explore</button>
          </div>
        </div>
      </div>
    </section>
  );
}

export default Hero;
