import React from 'react';
import { wishContext } from '../../Context/WishContext';

function AddWishButton({ product }) {
  const { addToWishList } = React.useContext(wishContext);
  const [isLoading, setIsLoading] = React.useState(false);

  const handleAddToWishList = async (productId) => {
    setIsLoading(true);
    const response = await addToWishList(productId);
    if (response) {
      setIsLoading(false);
    }
  };

  if (!localStorage.getItem('token')) {
    return <></>;
  }

  return (
    <>
      <div>
        <button
          onClick={() => {
            if (localStorage.getItem('wishList').includes(product.id)) {
              return;
            } else {
              handleAddToWishList(product.id);
            }
          }}
          disabled={
            !localStorage.getItem('token') ||
            localStorage.getItem('wishList').includes(product.id) ||
            isLoading
          }
          className="btn btn-circle btn-primary"
        >
          <span className="sr-only">Wishlist</span>
          {isLoading ? (
            <span className="loading loading-spinner"></span>
          ) : (
            <svg
              className="w-6 h-6"
              aria-hidden="true"
              xmlns="http://www.w3.org/2000/svg"
              width="24"
              height="24"
              fill="none"
              viewBox="0 0 24 24"
            >
              <path
                stroke="currentColor"
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth="2"
                d="M12.01 6.001C6.5 1 1 8 5.782 13.001L12.011 20l6.23-7C23 8 17.5 1 12.01 6.002Z"
              />
            </svg>
          )}
        </button>
      </div>
    </>
  );
}

export default AddWishButton;
