import React from 'react';

function BrandsButtons({ pageNumber, allProducts, setProductsByBrands }) {
  function filter(e) {
    if (e.target.innerText === 'All') {
      setProductsByBrands([]);
    } else {
      let name = e.target.innerText.toLowerCase();
      let newArr = allProducts.filter((item) =>
        item.brand.name.toLowerCase().includes(name)
      );
      setProductsByBrands(newArr);
    }
  }

  const brandsPageOne = [
    'All',
    'Adidas',
    'Canon',
    'DeFacto',
    'Dell',
    'Jack & Jones',
    'LC Waikiki',
    'Sony',
    'Samsung',
  ];

  const brandsPageTwo = [
    'All',
    'Dell',
    'Lenovo',
    'Samsung',
    'Sony',
    'Toshiba',
  ]

  return (
    <div>
      <div className="grid grid-cols-3 md:grid-cols-6 lg:grid-cols-9 gap-3 items-center justify-center mb-5">
        {pageNumber === 1
          ? brandsPageOne.map((brand, index) => (
              <button
                key={index}
                // onClick={() => filter({ target: { innerText: brand } })}
                onClick={filter}
                className="btn btn-outline btn-primary uppercase"
              >
                {brand}
              </button>
            ))
          : brandsPageTwo.map((brand, index) => (
              <button
                key={index}
                // onClick={() => filter({ target: { innerText: brand } })}
                onClick={filter}
                className="btn btn-outline btn-primary uppercase"
              >
                {brand}
              </button>
            ))}

        {/* {SpecificBrands?.map((brand) => {
          return (
            <button
              onClick={filter}
              key={brand._id}
              className="btn btn-outline btn-primary uppercase"
            >
              {brand.name}
            </button>
          );
        })} */}
      </div>
    </div>
  );
}

export default BrandsButtons;
