import React, { useEffect } from 'react';
// import Hero from '../Hero/Hero'
import HomePageSlider from '../Sliders/HomePageSlider';
import CategorySlider from '../Sliders/CategorySlider';
import Hero from './Hero/Hero';
import PopularSlider from '../Sliders/PopularSlider';
import Accordion from '../Accordion/Accordion';
import BrandSlider from '../Sliders/BrandSlider';


function Home() {
  window.scrollTo({ top: 0, behavior: 'smooth' });

  useEffect(() => {
    document.title = 'Home - Ecommerce';
  })

  return (
    <>
      <Hero />

      <div className="container mx-auto overflow-hidden">
        <HomePageSlider />
        <div className="container mx-auto">
          <section id="startExplore" className="text-center mt-20">
            <h1 className="text-3xl font-bold mb-3">Shop The Collections</h1>
            <p className="text-gray-400">
              Shop the latest products from the most popular collections
            </p>
            <div>
              <CategorySlider />
            </div>
          </section>

          <section id="popularProducts" className="text-center mt-20">
            <h1 className="text-3xl font-bold mb-3">Popular Products</h1>
            <p className="text-gray-400">
              Shop the latest products from the most popular products
            </p>
            <div>
              <PopularSlider />
            </div>
          </section>

          <section id="popularBrands" className="text-center mt-20">
            <h1 className="text-3xl font-bold mb-3">Our Partners</h1>
            <p className="text-gray-400">
              Shop the latest products from the most popular brands
            </p>
            <div>
              <BrandSlider />
            </div>
          </section>

          <section id="accordion" className="text-center my-20">
            <h1 className="text-3xl font-bold mb-3">FAQ</h1>
            <p className="text-gray-400"> Most frequent questions </p>
            <Accordion />
          </section>
        </div>
      </div>
    </>
  );
}

export default Home;
