import React, { createContext, useEffect, useState } from 'react';
import { CartClient, addToCartClient } from '../APIs/CartApi';
import { authContext } from './AuthContext';
import toast from 'react-hot-toast';

export const cartContext = createContext();

function CartContextProvider(props) {
  const { token } = React.useContext(authContext);

  const [numOfCartItems, setNumOfCartItems] = useState(0);
  const [totalPrice, setTotalPrice] = useState(0);
  const [cartItems, setCartItems] = useState(null);
  const [CartIsLoading, setCartIsLoading] = useState(false);
  const [userId, setUserId] = useState(null);
  const [cartId, setCartId] = useState(null);
  const [cartError, setCartError] = useState(null);

  const addToCart = async (productId) => {
    const response = await addToCartClient
      .post('', {
        productId: productId,
      })
      .then((res) => {
        if (res.data.status === 'success') {
          toast.success('Successfully Added!', {
            position: 'bottom-center',
            duration: 2000,
            style: {
              border: '1px solid #713200',
              padding: '16px',
              color: '#713200',
            },
          });
          setCartItems(res.data.data.products);
          setTotalPrice(res.data.data.totalCartPrice);
          setNumOfCartItems(res.data.numOfCartItems);
          getCartItems();
        }
        return true;
      })
      .catch((err) => {
        toast.error(`${err.message}`, {
          position: 'bottom-center',
          duration: 3000,
          style: {
            border: '1px solid red',
            padding: '16px',
            color: 'black',
          },
        });
        return false;
      });
    return response;
  };

  const updateQuantity = async (productId, quantity) => {
    const response = await CartClient.put(`/${productId}`, {
      'count': quantity,
    })
      .then((res) => {
        setCartItems(res.data.data.products);
        setTotalPrice(res.data.data.totalCartPrice);
        setNumOfCartItems(res.data.numOfCartItems);
        return true;
      })
      .catch((err) => {
        toast.error(`${err.message}`, {
          position: 'bottom-center',
          duration: 3000,
          style: {
            border: '1px solid red',
            padding: '16px',
            color: 'black',
          },
        });
        return false;
      });
    return response;
  };

  const deleteFromCart = async (productId) => {
    const response = await CartClient.delete(`/${productId}`)
      .then((res) => {
        setCartItems(res.data.data.products);
        setTotalPrice(res.data.data.totalCartPrice);
        setNumOfCartItems(res.data.numOfCartItems);
        return true;
      })
      .catch((err) => {
        toast.error(`${err.message}`, {
          position: 'bottom-center',
          duration: 3000,
          style: {
            border: '1px solid red',
            padding: '16px',
            color: 'black',
          },
        });
        return false;
      });
    return response;
  };

  const getCartItems = () => {
    setCartIsLoading(true);
    CartClient.get()
      .then((res) => {
        setCartId(res.data.data._id);
        setCartItems(res.data.data.products);
        setTotalPrice(res.data.data.totalCartPrice);
        setNumOfCartItems(res.data.numOfCartItems);
        setUserId(res.data.data.cartOwner);
        localStorage.setItem('userID', res.data.data.cartOwner);
        setCartIsLoading(false);
      })
      .catch((err) => {
        setCartIsLoading(false);
        setCartError(err.message);
      });
  };

  useEffect(() => {
    if (!token) {
      return;
    } else {
      getCartItems();
    }
  }, [token]);

  return (
    <>
      <cartContext.Provider
        value={{
          addToCart,
          updateQuantity,
          deleteFromCart,
          setCartIsLoading,
          numOfCartItems,
          cartItems,
          totalPrice,
          CartIsLoading,
          getCartItems,
          cartId,
          userId,
          cartError,
        }}
      >
        {props.children}
      </cartContext.Provider>
    </>
  );
}

export default CartContextProvider;
